import { Component, OnInit } from '@angular/core';

import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-ams-inbox-reports',
  templateUrl: './ams-inbox-reports.component.html',
  styleUrls: ['./ams-inbox-reports.component.scss']
})
export class AmsInboxReportsComponent implements OnInit {

  reportDataList:any;

  isDataLoaded:boolean = false;

  constructor(
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }



  ngOnInit() {

    let details = {
      ApartmentId: parseInt(this.cookieService.get('apartmentId')),
      LookupTypeId: 87, //lookuptypeid for report
      MenuName: 'Inbox'
    }

    this.lookupService.getLookupValuesByApartmentIdLookupTypeIdMenuName(details).subscribe((res:any) => {

      this.reportDataList = res;

      this.isDataLoaded = true;

    })


  }

}
