import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { AmsInboxComponent } from './ams-inbox.component';
import { AmsInboxSetupComponent } from './components/ams-inbox-setup/ams-inbox-setup.component';
import { AmsInboxNotifyComponent } from './components/ams-inbox-notify/ams-inbox-notify.component';
import { AmsInboxHistoryComponent } from './components/ams-inbox-history/ams-inbox-history.component';
import { AmsInboxReportsComponent } from './components/ams-inbox-reports/ams-inbox-reports.component';

const routes: Routes = [

	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: AmsInboxSetupComponent },
	{ path: 'notify', component: AmsInboxNotifyComponent },
	{ path: 'history', component: AmsInboxHistoryComponent },
	{ path: 'reports', component: AmsInboxReportsComponent },
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AmsInboxRoutingModule { }
