import { Component, OnInit } from '@angular/core';

import { UserService } from '../../../../api/services/user.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-ams-inbox-setup',
  templateUrl: './ams-inbox-setup.component.html',
  styleUrls: ['./ams-inbox-setup.component.scss']
})
export class AmsInboxSetupComponent implements OnInit {

  inboxCategoryData:any;

  isInboxCategoryLoaded:boolean = false;
  isInboxCategorySubmitted:boolean = true;

  isInboxCategoryNew:boolean = false;
  isInboxCategoryUpdate:boolean = false;

  inboxCategory:string = "";
  inboxCategoryOwner:string = "";
  inboxCategoryOwnerData:any;

  inboxCategoryUpdateId:number = 0;

  errorMessage:string = "";
  isError:boolean = false; 

  constructor(
  	private userService:UserService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  addNewInboxCategory(){
  	this.isInboxCategoryNew = true;
  	this.isInboxCategoryUpdate = false;
  }

  updateInboxCategory(item){
  	this.isInboxCategoryUpdate = true;
  	this.isInboxCategoryNew = false;
  	this.inboxCategory = item.lookupValueName;
  	this.inboxCategoryUpdateId = item.lookupValueId;
  }

  deleteInboxCategory(item, index){

  	this.isInboxCategoryLoaded = false;

  	var params = {
      lookupValueId: item.lookupValueId,
      updateUserId: parseInt(this.cookieService.get('userId'))
    }

  	this.lookupService.deleteLookupvalue(params).subscribe((res:any) => {

  	  this.inboxCategoryData.splice(index, 1);
	  this.isInboxCategoryLoaded = true;

	});

  }

  removeCategoryBox(){
  	this.isInboxCategoryNew = false;
  	this.isInboxCategoryUpdate = false;
  	this.isError = false;
  }

  ngOnInit() {

  	this.isInboxCategoryLoaded = true;

  	//get all users
    this.userService.getAllUsersByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
       
      this.inboxCategoryOwnerData = res;

    },
    error => {
      
    });

  }

}
